.message-container, .feedback-success-message-container, .feedback-error-message-container {
  border: 1px solid transparent; }

.feedback-success-message-container, .feedback-error-message-container {
  width: 100%;
  padding: 10px;
  margin: 15px 0 10px 0;
  color: #155724;
  background-color: rgba(212, 237, 218, 0.61);
  position: relative;
  border-radius: 4px 4px 0 0; }
  .feedback-success-message-container:after, .feedback-error-message-container:after {
    content: "";
    background: #76b852;
    background: -webkit-gradient(linear, left top, right top, from(#76b852), to(#8dc26f));
    background: -webkit-linear-gradient(left, #76b852 0%, #8dc26f 100%);
    background: -o-linear-gradient(left, #76b852 0%, #8dc26f 100%);
    background: linear-gradient(90deg, #76b852 0%, #8dc26f 100%);
    height: 2px;
    width: 100%;
    position: absolute;
    bottom: 0;
    left: 0; }

.feedback-error-message-container {
  color: #721c24;
  background-color: rgba(248, 215, 218, 0.61); }
  .feedback-error-message-container:after {
    background: #cb2d3e;
    background: -webkit-gradient(linear, left top, right top, from(#cb2d3e), to(#ef473a));
    background: -webkit-linear-gradient(left, #cb2d3e 0%, #ef473a 100%);
    background: -o-linear-gradient(left, #cb2d3e 0%, #ef473a 100%);
    background: linear-gradient(90deg, #cb2d3e 0%, #ef473a 100%); }



